import { Button, Card, CardContent, CardHeader, Stack, Typography } from "@mui/material";
import useMiddleware from "../../../../Application/useMiddleware";
import useFetch from "../../../../utils/useFetch";
import useFormData from "./form_data/useFormData";

export default function InvoiceQuickAccess() {
    const middleware = useMiddleware('Invoices.Main');

    const { data: products, loading, reload } = useFetch({
        handler: () => middleware.quickAccess(),
    });

    const { addRow } = useFormData();

    return (
        <Card variant="outlined">
            <CardHeader title="دسترسی سریع" titleTypographyProps={{ variant: "subtitle2" }} />
            <CardContent>
                {products.data?.length < 1 && <Typography textAlign="center">موردی وجود ندارد</Typography>}

                {products.data?.length > 0 && (
                    <Stack direction="row" flexWrap="wrap" gap={1}>
                        {products.data.map((product, index) => <Button
                            variant="outlined"
                            color="inherit"
                            key={index}
                            onClick={() => addRow(product)}
                        >{product.name}</Button>)}
                    </Stack>
                )}
            </CardContent>
        </Card>
    )
}