import sumBy from "lodash/sumBy";

class Utils {
    static getItemPureTotal = (item) => item.amount * item.unitPrice;
    static getItemDiscountPrice = (item) => item.discount || 0;
    static getItemTotalPrice = (item) => this.getItemPureTotal(item) - this.getItemDiscountPrice(item);
    // static getItemTotalPrice = (item) => this.getItemPureTotal(item) - (item.discount || 0);

    static getFactorPrice = (items, factor) => {
        return (
            factor.unit === 'PRICE' ?
                factor.amount :
                (factor.amount / 100 * this.getInvoiceSubtotal(items))
        ) * (factor.type === 'REDUCER' ? -1 : 1);
    }

    static getInvoiceSubtotal = (items) => sumBy(items, (itm) => this.getItemTotalPrice(itm));
    static getInvoiceTotal = (items, factors) => Number(this.getInvoiceSubtotal(items)) +
        sumBy(factors, factor => this.getFactorPrice(items, factor));
}

export default Utils