import { Icon as Iconify } from "@iconify/react";
import IconAdd from "@mui/icons-material/Add";
import IconRemove from "@mui/icons-material/Remove";
import { Box, Button, Card, CardActions, CardContent, CardHeader, Divider, IconButton, InputAdornment, Stack, Table, TableBody, TableCell, TableContainer, TableHead, TableRow, Tooltip, Typography, styled } from "@mui/material";
import flatten from "lodash/flatten";
import isFinite from "lodash/isFinite";
import sortBy from "lodash/sortBy";
import { useMemo, useState } from "react";
import { useTranslation } from "react-i18next";
import { useComponent } from "../../../../Application";
import { useAuth } from "../../../../auth";
import NumberTextField from "../../../../components/NumberTextField";
import pricify from "../../../../utils/pricify";
import Utils from "../../Utils";
import EditableInlineField from "../../components/EditableInlineField";
import StyledTableCell from "../../components/StyledTableCell";
import { useFormData } from "./form_data";

const StyledTableHead = styled(TableHead)(({ theme }) => ({
    '& .MuiTableCell-root': {
        backgroundColor: theme.palette.background.paper
    }
}));

export default function InvoiceItems({ injectedHooks = {} }) {
    const { inject } = useAuth();
    const { t } = useTranslation(["global", "invoices"]);

    const { getValue, setValue, addRow, removeRow, ...form } = useFormData();

    const subtotal = useMemo(() => Utils.getInvoiceSubtotal(getValue('items')), [getValue('items')])

    const ProductSelectDialog = useComponent('Products.ProductSelectDialog');
    const [productSelectDialogProps, setProductSelectDialogProps] = useState({ open: false });

    const tableDefs = sortBy([
        {
            title: '#',
            headerCellProps: {
                align: "center",
            },
            rowCellProps: {
                align: "center",
                width: 75
            },
            renderCell: (row, index) => (
                <Typography fontSize="1.25rem" fontWeight={900}>{index + 1}</Typography>
            ),
            priority: 100
        },
        {
            title: t("کالا/خدمت"),
            headerCellProps: {
                align: "center"
            },
            rowCellProps: (row, index) => ({
                align: "center",
                width: 200,
                onClick: () => setProductSelectDialogProps({ open: true, rowIndex: index, selected: row.item }),
                sx: { cursor: 'pointer' }
            }),
            renderCell: (row, index) => row.item?.name || t('انتخاب کنید...'),
            priority: 200
        }, {
            title: t("مقدار"),
            headerCellProps: {
                align: "center"
            },
            rowCellProps: {
                align: "center",
                width: 175
            },
            renderCell: (row, index) => (
                <Box sx={{ display: "flex", justifyContent: 'center', alignItems: 'center' }}>
                    <IconButton
                        size="small"
                        onClick={() => setValue(`items[${index}].amount`, (row.amount ?? 1) - 1)}
                        disabled={row.amount < 2}
                    >
                        <IconRemove fontSize="inherit" />
                    </IconButton>
                    <Typography sx={{ mx: 1, verticalAlign: 'middle' }}>
                        {row.amount}
                    </Typography>
                    <IconButton
                        size="small"
                        onClick={() => setValue(`items[${index}].amount`, (row.amount ?? 1) + 1)}
                    // disabled={getProductMaxQty(itm.product) < 1}
                    >
                        <IconAdd fontSize="inherit" />
                    </IconButton>
                </Box>
            ),
            priority: 300
        }, {
            title: <>{t("قیمت واحد")} <Typography variant="caption">(ریال)</Typography></>,
            headerCellProps: {
                align: "center",
                width: 175,
            },
            rowCellComponent: EditableInlineField,
            rowCellProps: (row, index) => ({
                align: "center",
                width: 175,
                defaultValue: row.unitPrice,
                onSubmit: newValue => setValue(`items[${index}].unitPrice`, newValue),
                InlineComponent: StyledTableCell,
                renderInlineValue: pricify,
                inlineComponentProps: { align: "center" },
                renderForm: ({ value, setValue, toFocusRef }) => (
                    <StyledTableCell align="center">
                        <NumberTextField
                            fullWidth={true}
                            size="small"
                            dir="ltr"
                            InputProps={{
                                inputRef: toFocusRef,
                                startAdornment: <InputAdornment position="start"><small>ریال</small></InputAdornment>
                            }}
                            value={value}
                            onChange={e => setValue(e.target.value)}
                            required
                        // error={Boolean(errors.unitPrice)}
                        />
                    </StyledTableCell>
                )
            }),
            renderCell: (row, index) => null,
            priority: 400
        }, {
            title: <>{t("تخفیف")} <Typography variant="caption">(ریال)</Typography></>,
            headerCellProps: {
                align: "center",
                width: 175
            },
            rowCellComponent: EditableInlineField,
            rowCellProps: (row, index) => ({
                align: "center",
                width: 175,
                defaultValue: row.discount,
                onSubmit: newValue => setValue(`items[${index}].discount`, newValue),
                InlineComponent: StyledTableCell,
                renderInlineValue: pricify,
                inlineComponentProps: { align: "center" },
                renderForm: ({ value, setValue, toFocusRef }) => (
                    <StyledTableCell align="center">
                        <NumberTextField
                            fullWidth={true}
                            size="small"
                            dir="ltr"
                            InputProps={{
                                inputRef: toFocusRef,
                                startAdornment: <InputAdornment position="start"><small>ریال</small></InputAdornment>
                            }}
                            value={value}
                            onChange={e => setValue(e.target.value)}
                            required
                        // error={Boolean(errors.discount)}
                        />
                    </StyledTableCell>
                )
            }),
            renderCell: (row, index) => null,
            priority: 500
        }, {
            title: <>{t("مبلغ خالص")} <Typography variant="caption">(ریال)</Typography></>,
            headerCellProps: {
                align: "center"
            },
            rowCellProps: {
                align: "center",
                width: 175
            },
            renderCell: (row, index) => {
                const total = Utils.getItemTotalPrice(row);
                return isFinite(total) ? pricify(total) : 0;
            },
            priority: 600
        },
        ...flatten((inject('Invoices', 'InvoiceForm.tableDefinitions', { getValue, setValue, addRow, removeRow, ...form, ...injectedHooks }) || []))
    ], o => o.priority);

    return (
        <>
            <Card variant="outlined" sx={{ height: '100%', display: 'flex', flexDirection: 'column' }}>
                <CardHeader
                    title="اقلام فاکتور"
                    action={
                        <Button
                            size="small"
                            variant="contained"
                            startIcon={<Iconify icon="solar:add-circle-linear" />}
                            onClick={() => addRow()}
                        >
                            ردیف جدید
                        </Button>
                    }
                    titleTypographyProps={{ variant: "subtitle2" }}
                />
                <CardContent sx={{ p: 0 }}>
                    <TableContainer sx={{ maxHeight: '400px', overflow: 'auto' }}>
                        <Table size="small" stickyHeader>
                            <StyledTableHead>
                                <TableRow>
                                    {tableDefs.map((def, index) => {
                                        if (!def.render && !def.renderCell && !def.field)
                                            return null;

                                        return (
                                            <TableCell key={index} {...def.headerCellProps} sx={{ whiteSpace: "nowrap" }}>
                                                {def.title}
                                            </TableCell>
                                        )
                                    })}
                                    <TableCell />
                                </TableRow>
                            </StyledTableHead>
                            <TableBody>
                                {getValue('items').map((row, index) => {
                                    return (
                                        <TableRow key={index}>
                                            {tableDefs.map((def, defIndex) => {
                                                if (!def.render && !def.renderCell && !def.field)
                                                    return null;

                                                const CellComponent = def.rowCellComponent || StyledTableCell;

                                                return (
                                                    <CellComponent key={defIndex} {...(typeof def.rowCellProps == "function" ? def.rowCellProps(row, index) : def.rowCellProps)}>
                                                        {def.renderCell ? def.renderCell(row, index) : (def.render ? def.render(row, index) : row[def.field])}
                                                    </CellComponent>
                                                )
                                            })}

                                            <StyledTableCell align="center">
                                                <Stack direction="row" alignItems="center" spacing={1}>
                                                    <Tooltip title="حذف سطر" enterDelay={2000}>
                                                        <IconButton size="small" onClick={() => removeRow(index)}>
                                                            <Iconify icon="solar:trash-bin-2-broken" />
                                                        </IconButton>
                                                    </Tooltip>
                                                </Stack>
                                            </StyledTableCell>
                                        </TableRow>
                                    )
                                })}
                            </TableBody>
                        </Table>
                    </TableContainer>
                </CardContent>
                <Divider sx={{ mt: 'auto' }} />
                <CardActions sx={{ justifyContent: "flex-end" }}>
                    <Typography variant="subtitle2">مجموع: {isFinite(subtotal) ? pricify(subtotal) : 0} <Typography variant="caption">ریال</Typography></Typography>
                </CardActions>
            </Card>

            <ProductSelectDialog
                {...productSelectDialogProps}
                onSelect={addRow}
                onClose={() => setProductSelectDialogProps({ open: false })}
            />
        </>
    )
}