import AdapterJalali from '@date-io/date-fns-jalali';
import { Icon as Iconify } from "@iconify/react";
import {
    Box,
    Button,
    Card, CardContent, CardHeader, Collapse,
    Stack,
    styled,
    SvgIcon,
    TextField,
    Typography
} from "@mui/material";
import { DatePicker } from "@mui/x-date-pickers";
import { LocalizationProvider } from '@mui/x-date-pickers/LocalizationProvider';
import format from "date-fns-jalali/format";
import { useState } from 'react';
import { useTranslation } from 'react-i18next';
import { useComponent } from "../../../../Application";
import NumberTextField from "../../../../components/NumberTextField";
import SingleFieldDialog from "../../../../components/SingleFieldDialog";
import useDialog from "../../../../utils/useDialog";
import EditableInlineField from '../../components/EditableInlineField';
import { useFormData } from './form_data';


const StyledEditableTypography = styled(Typography)(({ theme }) => ({
    position: 'relative',
    cursor: "pointer",
    transition: theme.transitions.create('padding'),

    '& .MuiSvgIcon-root': {
        position: 'absolute',
        right: 0,
        opacity: 0,
        transition: theme.transitions.create('opacity')
    },

    '&:hover': {
        paddingInlineEnd: '1.25em',
    },

    '&:hover .MuiSvgIcon-root': {
        opacity: 1,
    }
}));

function EditableTypography({ children, ...props }) {
    return (
        <StyledEditableTypography {...props}>
            {children}
            <SvgIcon fontSize="small" sx={{ verticalAlign: 'middle' }}>
                <Iconify icon="solar:pen-linear" />
            </SvgIcon>
        </StyledEditableTypography>
    )
}


const InvoiceGeneral = () => {
    const { t } = useTranslation(["global", "invoices"]);

    const { getValue, setValue } = useFormData();

    const [expanded, setExpanded] = useState(false);

    const PersonSelectDialog = useComponent("People.PersonSelectDialog");
    const {
        isOpen: isCustomerDialogOpen,
        close: closeCusomersDialog,
        open: openCustomersDialog
    } = useDialog(false);


    const PricingSelectDialog = useComponent("Pricing.PricingsDialog");
    const {
        isOpen: isPricingsDialogOpen,
        close: closePricingsDialog,
        open: openPricingsDialog
    } = useDialog(false);

    const [fieldDialogProps, setFieldDialogProps] = useState({ open: false });
    const openField = (renderContent, options = {}) => {
        return () => {
            setFieldDialogProps({
                renderContent,
                ...options,
                open: true,
            });
        };
    };

    const data = [
        {
            label: 'وضعیت',
            value: getValue('status')
        },
        {
            label: 'مشتری',
            value: getValue('customer')?.fullName || 'مشتری متفرقه',
            onClick: openCustomersDialog
        },
        {
            label: 'تاریخ',
            renderValue: (
                <EditableInlineField
                    defaultValue={getValue('issuedAt')}
                    onSubmit={issuedAt => setValue('issuedAt', issuedAt)}
                    InlineComponent={Typography}
                    inlineComponentProps={{
                        fontWeight: 600,
                        variant: "body1",
                        sx: { display: "flex", justifyContent: 'flex-end', width: '100%' }
                    }}
                    renderInlineValue={val => format(val, 'yyyy/MM/dd')}
                    renderForm={({ value, setValue, toFocusRef }) => (
                        <Box>
                            <LocalizationProvider dateAdapter={AdapterJalali}>
                                <DatePicker
                                    mask="____/__/__"
                                    onChange={setValue}
                                    value={value}
                                    renderInput={
                                        (params) => (
                                            <TextField
                                                InputLabelProps={{ shrink: true }}
                                                fullWidth={true}
                                                size="small"
                                                inputRef={toFocusRef}
                                                {...params}
                                            />
                                        )
                                    }
                                />
                            </LocalizationProvider>
                        </Box>
                    )}
                />
            ),
        },
        {
            label: 'شماره',
            renderValue: (
                <EditableInlineField
                    defaultValue={getValue('number')}
                    onSubmit={number => setValue('number', number)}
                    InlineComponent={Typography}
                    inlineComponentProps={{
                        fontWeight: 600,
                        variant: "body1",
                        sx: { display: "flex", justifyContent: 'flex-end', width: '100%' }
                    }}
                    renderForm={({ value, setValue, toFocusRef }) => (
                        <NumberTextField
                            fullWidth={true}
                            size="small"
                            dir="ltr"
                            value={value}
                            onChange={e => setValue(e.target.value)}
                            required
                            inputProps={{ ref: toFocusRef }}
                        // error={Boolean(errors.unitPrice)}
                        />
                    )}
                />
            )
        },
    ]

    const expandedData = [
        {
            label: 'تعرفه',
            value: getValue('pricing')?.name || 'تعرفه پیش‌فرض',
            onClick: openPricingsDialog
        },
        {
            label: 'توضیحات',
            value: getValue('note') || <Typography color="inherit" sx={{ opacity: 0.5 }} display="inline">شرح دهید...</Typography>,
            props: { direction: "column", alignItems: "flex-start" },
            onClick: openField(
                ({ value, setValue }) => (
                    <TextField
                        InputLabelProps={{ shrink: true }}
                        fullWidth={true}
                        multiline
                        rows={3}
                        label="توضیحات فاکتور"
                        value={value}
                        onChange={(e) =>
                            setValue(e.target.value)
                        }
                        autoFocus
                    />
                ),
                {
                    title: "توضیحات فاکتور",
                    defaultValue: getValue('note'),
                    onSubmit: (note) => {
                        setValue('note', note)
                        setFieldDialogProps({ open: false });
                    },
                }
            )
        },
    ]

    const renderDataList = (data) => {
        return (
            <Stack>
                {data.map((row, index) => (
                    <Stack direction="row" alignItems="center" key={index} sx={{ mb: 2 }} {...(row.props ?? {})}>
                        <Typography variant="body1" sx={{ flexGrow: 1 }}>{row.label}</Typography>

                        <Box display="inline-flex" justifyContent="flex-end" alignItems="center" flexGrow="1">
                            {row.renderValue || (
                                <EditableTypography
                                    sx={{ marginInlineStart: "auto" }}
                                    onClick={row.onClick}
                                    fontWeight={600}
                                    variant="body1"
                                >
                                    {row.value}
                                </EditableTypography>
                            )}
                        </Box>
                    </Stack>
                ))}
            </Stack>
        )
    }

    return (
        <>
            <Card variant="outlined">
                <CardHeader title="عمومی" titleTypographyProps={{ variant: "subtitle2" }} />
                <CardContent>
                    {renderDataList(data)}

                    <Collapse in={expanded}>
                        {renderDataList(expandedData)}
                    </Collapse>

                    <Button
                        endIcon={<Iconify icon="solar:alt-arrow-down-bold" />} sx={{ marginInlineStart: "auto" }}
                        onClick={() => setExpanded(!expanded)}
                    >
                        {t("مشخصات بیشتر")}
                    </Button>
                </CardContent>
            </Card>

            <PersonSelectDialog
                open={isCustomerDialogOpen}
                onClose={closeCusomersDialog}
                selected={getValue('customer')}
                roles={['customer']}
                onSelect={(customer) => {
                    setValue('customer', customer);
                    closeCusomersDialog();
                }}
                onClear={() => {
                    setValue('customer', '');
                    closeCusomersDialog();
                }}
            />

            <PricingSelectDialog
                selectable={true}
                open={isPricingsDialogOpen}
                onClose={closePricingsDialog}
                selected={getValue('pricing')}
                onChange={(pricing) => {
                    setValue('pricing', pricing);
                    closePricingsDialog();
                }}
                onClear={() => {
                    setValue('pricing', '');
                    closePricingsDialog();
                }}
            />

            <SingleFieldDialog
                {...fieldDialogProps}
                onClose={() => setFieldDialogProps({ open: false })}
            />
        </>
    )
}

export default InvoiceGeneral