import { Button, Card, CardActions, CardContent, CardHeader } from "@mui/material";
import { useFormData } from "./form_data";
import { useAuth } from "../../../../auth";

export default function InvoicePayments({ injectedHooks }) {
    const { inject } = useAuth();
    const { register, handleSubmit, loading, reset, getValue, setValue } = useFormData();

    return (
        <Card variant="outlined">
            <CardHeader title="پرداخت" titleTypographyProps={{ variant: "subtitle2" }} />
            <CardContent>
                {inject('Invoices', 'InvoiceForm.Payment', { register, loading, getValue, setValue, reset, ...injectedHooks })}
            </CardContent>
            <CardActions>
                <Button
                    variant="contained"
                    size="large"
                    fullWidth
                    // disabled={loading}
                    onClick={handleSubmit}
                >ایجاد فاکتور</Button>
            </CardActions>
        </Card>
    )
}