import CloseIcon from "@mui/icons-material/CloseOutlined";
import {
	Box,
	Button,
	DialogActions,
	DialogContent,
	DialogTitle,
	Divider,
	IconButton,
} from "@mui/material";
import { useEffect, useState } from "react";

import ResponsiveDialog from "./ResponsiveDialog";

const SingleFieldDialog = ({
	title,
	defaultValue,
	onSubmit,
	open,
	onClose,
	renderContent,
	dialogProps = {},
}) => {
	const [value, setValue] = useState(defaultValue);

	useEffect(() => {
		if (open) setValue(defaultValue);
	}, [open]);

	return (
		<ResponsiveDialog
			open={open}
			onClose={onClose}
			dialogProps={{ fullWidth: true, maxWidth: "xs", ...dialogProps }}
		>
			<DialogTitle
				sx={{
					display: "flex",
					justifyContent: "space-between",
					alignItems: "center",
				}}
			>
				<Box>{title}</Box>
				<IconButton onClick={onClose} size="small">
					<CloseIcon />
				</IconButton>
			</DialogTitle>
			<Divider />
			<DialogContent>
				{renderContent && renderContent({ value, setValue })}
			</DialogContent>
			<Divider />
			<DialogActions>
				<Button
					type="submit"
					variant="contained"
					onClick={(e) => onSubmit(value)}
				>
					ثبت
				</Button>
				<Box sx={{ flexGrow: 1 }}></Box>
				<Button type="button" onClick={onClose}>
					انصراف
				</Button>
			</DialogActions>
		</ResponsiveDialog>
	);
};

export default SingleFieldDialog;
