import { Grid } from "@mui/material";
import { useTask } from "../../../../Application";
import ResponsiveContainer from "../../../../components/ResponsiveContainer";
import { TabView } from "../../../../tabs";
import { FormDataProvider } from "./form_data";
import InvoiceGeneral from "./InvoiceGeneral";
import InvoiceTable from "./InvoiceTable";
import InvoicePayments from "./InvoicePayments";
import InvoiceQuickAccess from "./InvoiceQuickAccess";
import { useAuth } from "../../../../auth";

const InvoiceForm = () => {
    const { inject } = useAuth();
    const task = useTask('Invoices.create');

    const injectedHooks = Object.assign({}, ...(inject('Invoices', 'InvoiceForm.Hooks') || []));

    return (
        <FormDataProvider injectedHooks={injectedHooks}>
            <TabView {...task}>
                <ResponsiveContainer maxWidth="xl" fullHeight>
                    <Grid container spacing={2}>
                        <Grid item xs={3}>
                            <Grid container spacing={2}>
                                <Grid item xs={12}>
                                    <InvoiceGeneral />
                                </Grid>
                                <Grid item xs={12}>
                                    <InvoiceQuickAccess />
                                </Grid>
                            </Grid>
                        </Grid>
                        <Grid item xs={9} container justifyContent="end" spacing={2}>
                            <Grid item xs={12}>
                                <InvoiceTable injectedHooks={injectedHooks} />
                            </Grid>
                            <Grid item xs={4}>
                                <InvoicePayments injectedHooks={injectedHooks} />
                            </Grid>
                        </Grid>
                    </Grid>
                </ResponsiveContainer>
            </TabView>
        </FormDataProvider>
    )
}

export default InvoiceForm;