
import { useEffect } from "react";
import { useMiddleware, useTask } from "../../../../../Application";
import { useAuth } from "../../../../../auth";
import useForm from "../../../../../utils/useForm";
import FormDataContext from "./FormDataContext";
import { useSnackbar } from "notistack";
import useNavigateTask from "../../../../../utils/useNavigateTask";


const calculateUnitPrice = (product, formPricing) => {
    if (!product || !formPricing || !product.prices?.length) return 0;
    const matchingPrice = product.prices.find(price => price.pricing._id === formPricing.id);
    return matchingPrice?.amount ?? 0;
};

export default function FormDataProvider({ injectedHooks = {}, defaultValues = {}, children }) {
    const { inject } = useAuth();
    const middleware = useMiddleware('Invoices.Main')

    const createNewItem = (product, pricing) => ({
        item: product,
        amount: 1,
        discount: 0,
        unitPrice: calculateUnitPrice(product, pricing),
        ...Object.assign({}, ...(inject('Invoices', 'InvoiceForm.newItemValues') || [])),
    });

    const initialValues = {
        status: '',
        customer: null,
        issuedAt: new Date(),
        number: '',
        pricing: null,
        note: '',
        items: [createNewItem()],
        ...Object.assign({}, ...(inject('Invoices', 'InvoiceForm.initialValues') || [])),
        ...defaultValues
    }


    const { enqueueSnackbar } = useSnackbar();

    const navigateTask = useNavigateTask();
    const taskList = useTask('Invoices.list');

    const form = useForm({
        initialValues,
        handlePost: data => middleware.createOrUpdate({ ...data }),
        onSubmit: () => {
            enqueueSnackbar('فاکتور ثبت گردید.');
            navigateTask(taskList, { replace: true })
        }
    })

    const initializeFormAsync = async () => {
        let response = await middleware.getDefaultPricing();
        const pricing = response.pricing;

        let number = defaultValues.number;
        if (!defaultValues.id) {
            response = await middleware.getNewNumber();
            number = response.number;
        }

        form.reset({ ...initialValues, number, pricing });
    }

    useEffect(() => {
        initializeFormAsync();
    }, []);

    const addRow = (product = null) => {
        const items = form.getValue('items');
        const pricing = form.getValue('pricing');

        if (product) {
            // Check if the product already exists in the items array
            const existingItemIndex = items.findIndex(item => item.item?._id === product._id);

            if (existingItemIndex !== -1) {
                // If the product exists, increment its amount
                items[existingItemIndex].amount += 1;
            } else if (items.length > 0 && !items[items.length - 1].item) {
                // If the last item's `item` is empty, update it with the new product
                items[items.length - 1] = {
                    ...items[items.length - 1],
                    item: product,
                    unitPrice: calculateUnitPrice(product, pricing)
                };
            } else {
                // Otherwise, add a new item
                items.push(createNewItem(product, pricing));
            }
        } else {
            // If no product is provided, add a new empty item
            items.push(createNewItem(null, pricing));
        }

        form.setValue('items', items);
    };

    const removeRow = (index) => {
        const items = form.getValue('items');
        const pricing = form.getValue('pricing');
        form.setValue('items', index === 0 && items.length === 1 ? [createNewItem(null, pricing)] : items.filter((i, idx) => idx !== index))
    }

    return (
        <FormDataContext.Provider value={{ ...form, addRow, removeRow }}>
            {children}

            {inject('Invoices', 'InvoiceForm.Footer', { ...form, ...injectedHooks })}
        </FormDataContext.Provider>
    )
}